import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import Valikko from './components/Valikko';
import Aloitus from './components/Aloitus';
import Laskuri from './components/Laskuri';
import Linkit from './components/Linkit';
import Tulos from './components/Tulos';
import TulosTuotto from './components/TulosTuotto';
import { useState } from 'react';
import Footer from './components/Footer';
import Tehtavalista from "./components/Tehtavalista";
import PoistaTehtava from "./components/PoistaTehtava";
import UusiTehtava from "./components/UusiTehtava";


function App() {

  const [lasku, setLasku] = useState({});
  const [tehtavat, setTehtavat] = useState([]);

  return (
    <Router >
      <Valikko />
      <Routes>
        <Route path="/laskentaa/" exact element={<Aloitus />} />
        <Route path="/linkit" element={<Linkit />} />
        <Route path="/laskuri" element={<Laskuri lasku={lasku} setLasku={setLasku} />} />
        <Route path="/tulos" element={<Tulos lasku={lasku} setLasku={setLasku}/>} />
        <Route path="/tulostuotto" element={<TulosTuotto lasku={lasku} setLasku={setLasku}/>} />
        <Route path="/tehtavat" exact element={<Tehtavalista tehtavat={tehtavat} setTehtavat={setTehtavat}/>}/>
        <Route path="/lisays" element={<UusiTehtava tehtavat={tehtavat} setTehtavat={setTehtavat}/>}/>
        <Route path="/poisto/:id" element={<PoistaTehtava tehtavat={tehtavat} setTehtavat={setTehtavat}/>}/>
      </Routes>
      <Footer/>
    </Router>
  );
}

export default App;
