
import { Button, Typography } from "@mui/material";
import { Link, useNavigate, useParams } from 'react-router-dom';


function PoistaTehtava(props) {

    const navigate = useNavigate();
    const { id } = useParams();

    const poistettavaTehtava = props.tehtavat.filter((tehtava) => {
        return (tehtava.id ===id);
    })[0];

    const kasitteleLomake = (e) => {
        
        e.preventDefault();

        let tehtavatApu = props.tehtavat.filter((tehtava) =>{
            return (tehtava.id !== id);
        });
        props.setTehtavat([...tehtavatApu]);
        navigate("/tehtavat");
    }

    return (
        <form onSubmit={kasitteleLomake}>
            <h4>Vahvista tehtvn poistaminen</h4>

            <Typography>
                Haluatko varmasti poistaa tehtvn {poistettavaTehtava.nimi}?
            </Typography>

            <Button
                style={{marginBottom:"5px"}}
                type="submit"
                variant="contained"
                color="primary"
                fullWidth
            >Poista Tehtv</Button>
            <Button
                component={Link}
                to="/tehtavat"
                variant="contained"
                color="primary"
                fullWidth
            >Peruuta</Button>
        </form>
    );
}

export default PoistaTehtava;