import { Button, Container, Typography } from "@mui/material";
import { Link } from "react-router-dom";

function Tulos(props) {

    let kksera = 1
    let sera = Number(props.lasku.ssumma) * Math.pow((1 + Number(0.01 * props.lasku.skorko)), props.lasku.saika);
    if (props.lasku.kkssumma) {
        kksera = (Number(props.lasku.kkssumma * 12)) * (1 - Math.pow((1 + Number(0.01 * props.lasku.skorko)), Number(props.lasku.saika))) / (1 - (1 + Number(0.01 * props.lasku.skorko)))
        sera += kksera
    }
    return (
        <Container>
            <Typography>
                Tssp ne tiedot!
            </Typography>
            <Typography>
                <p>Sijoitusaika: {props.lasku.saika} vuotta</p> <p>sijoitettava aloitussumma on {props.lasku.ssumma}</p>
                {(props.lasku.kkssumma) ?
                    <p>Tuottoprosentti on  {props.lasku.skorko}%, tekee korkoa korolle yhteens {((kksera + (Number(props.lasku.ssumma) * Math.pow((1 + Number(0.01 * props.lasku.skorko)), props.lasku.saika) - props.lasku.ssumma)) - props.lasku.kkssumma * 12 * props.lasku.saika).toFixed(2)} </p>
                    : <p>Tuottoprosentti on {props.lasku.skorko}%, tekee korkoa korolle yhteens {(Number(props.lasku.ssumma) * Math.pow((1 + Number(0.01 * props.lasku.skorko)), props.lasku.saika) - props.lasku.ssumma).toFixed(2)}
                         koko ajalta </p>}
                {(props.lasku.kkssumma) ? <p>Kuukausisstist kertyy {Number(props.lasku.saika) * 12 * Number(props.lasku.kkssumma)}</p> : null}

            </Typography>
            <Typography variant="h6">
                Sijoitushorisontissa siint {sera.toFixed(2)}
            </Typography>

            <Button
                component={Link}
                to="/laskuri"

                variant="contained"
                color="primary"
                size="large"
                fullWidth="true"
            >
                Takaisin laskuriin
            </Button>
            <Typography style={{ fontStyle: "italic", marginTop: "10px" }}>
                Laskettu vuosikorolla, laskurin tiedot ovat suuntaa antavia.
            </Typography>
        </Container>
    );
}

export default Tulos;