import { Button, Container, IconButton, List, ListItem, ListItemIcon, ListItemText } from "@mui/material";
import CheckBoxIcon from '@mui/icons-material/CheckBox';
import Delete from '@mui/icons-material/Delete';
import CheckBoxOutlineBlank from '@mui/icons-material/CheckBoxOutlineBlank';
import { Link } from "react-router-dom";

function Tehtavalista(props){
    const suoritettu = (indeksi) => {
        let tehtavatApu = [...props.tehtavat];
        tehtavatApu[indeksi].suoritettu = !tehtavatApu[indeksi].suoritettu;
        props.setTehtavat([...tehtavatApu]);
    }

    return(
        <Container>
            <h4>Lista</h4>
            <Button
                component={Link}
                to="/lisays"
                variant="contained"
                color="primary"
                fullwidth="true"
            >
                Lis uusi tehtava
            </Button>

            {(Object.entries(props.tehtavat).length > 0) ?
            <List>
            {props.tehtavat.map((tehtava, idx) => {
                return (
                    <ListItem key={idx}>
                        <ListItemIcon>
                            <IconButton
                                onClick={()=>{suoritettu(idx)}}
                            >
                                {(tehtava.suoritettu)
                                    ?<CheckBoxIcon />
                                    :<CheckBoxOutlineBlank />}
                            </IconButton>
                        </ListItemIcon>
                        <ListItemText primary={tehtava.nimi} />
                        <ListItemIcon>
                            <IconButton
                                edge="end"
                                component={Link}
                                to={`/poisto/${tehtava.id}`}
                            >
                                <Delete />
                            </IconButton>
                        </ListItemIcon>
                    </ListItem>
                    
                );
            })}
            </List>
            : <h4>Tyhj lista, lis jotain</h4>}
        </Container>
    )
}

export default Tehtavalista;