import { Button, TextField } from "@mui/material";
import { useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DateAdapter from '@mui/lab/AdapterDateFns';
import DatePicker from "@mui/lab/DatePicker";
import { fi } from "date-fns/locale";
import { v4 as uuid} from "uuid";


function UusiTehtava(props) {

    const tekstikentta = useRef();
    const navigate = useNavigate();
    const [value, setValue] = useState();

    const kasitteleLomake = (e) => {

        e.preventDefault();

        let uusiTehtavaApu = {
            id : uuid(),
            nimi: tekstikentta.current.value,
            suoritettu: false
        }
        props.setTehtavat([...props.tehtavat, uusiTehtavaApu]);
        navigate("/tehtavat")
    }

    return (
        <LocalizationProvider dateAdapter={DateAdapter} locale={fi}>
            <form onSubmit={kasitteleLomake}>
                <h4>Lis uusi tehtv</h4>

                <TextField
                    style={{marginBottom:"10px"}}
                    variant="outlined"
                    fullWidth
                    label="Kirjoita tehtv ja paina enter"
                    placeholder="Mit on tehtv"
                    inputRef={tekstikentta}
                />

                <DatePicker
                    label="takaraja"
                    value={value}
                    onChange={(newValue) => {
                        setValue(newValue);
                    }}
                    renderInput={(params) => <TextField {...params} />}

                />

                <Button
                    style={{marginTop:"10px"}}
                    type="submit"
                    variant="contained"
                    color="primary"
                    fullWidth
                >Lis tehtv</Button>
            </form>
        </LocalizationProvider>
    )
}

export default UusiTehtava;