import { Button, Container, Typography } from "@mui/material";
import { Link } from "react-router-dom";

function Tulos(props) {

    let kuukausiera = (Number(props.lasku.lainasumma) + (Number(props.lasku.lainasumma) * Number(0.01 * props.lasku.korko) * Number(props.lasku.aika))) / (Number(props.lasku.aika) * 12);
    if (props.lasku.viimeera) {
        kuukausiera = ((Number(props.lasku.lainasumma) - Number(props.lasku.viimeera)) + (Number(props.lasku.lainasumma) * Number(0.01 * props.lasku.korko) * Number(props.lasku.aika))) / (Number(props.lasku.aika) * 12);
    }
    return (
        <Container>
            <Typography>
                Tssp ne tiedot!
            </Typography>
            <Typography>
                <p>Laina-aika on: {props.lasku.aika} vuotta</p> <p>lainattava summa on {props.lasku.lainasumma}</p>
                <p>korkoprosentti on {props.lasku.korko}%, tekee maksettavaa korkoa on yhteens {Number(props.lasku.lainasumma) * Number(0.01 * props.lasku.korko) * Number(props.lasku.aika)}
                     koko ajalta </p>

            </Typography>
            <Typography variant="h6">

                Kuukausier on {kuukausiera.toFixed(2)}
                {props.lasku.viimeera ? <p>viimene iso er on {props.lasku.viimeera}</p> : null}
            </Typography>

            <Button
                component={Link}
                to="/laskuri"

                variant="contained"
                color="primary"
                size="large"
                fullWidth="true"
            >
                Takaisin laskuriin
            </Button>
            <Typography style={{ fontStyle: "italic", marginTop: "10px" }}>
                Laskurin tiedot ovat suuntaa antavia.
            </Typography>
        </Container>
    );
}

export default Tulos;