import { BottomNavigation, CssBaseline, Paper } from "@mui/material";

function Footer() {
    return (
        <CssBaseline>
            <BottomNavigation
                width="100%"
                style={{
                    background: "lightblue",
                    position:"fixed",
                    bottom: "0",
                    left: "0",
                    right: "0"
                }}
            >
                <p>&copy; 2022 MattiPrdct</p>
            </BottomNavigation>
        </CssBaseline>
    );
}

export default Footer;