import { List, Toolbar, AppBar, IconButton, Drawer, ListItem, ListItemText, ListItemIcon, CssBaseline, Typography } from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import HomeIcon from "@mui/icons-material/Home";
import InfoIcon from "@mui/icons-material/Info";
import { useState } from "react";
import { Link } from "react-router-dom";

function Valikko() {

    const [naytaValikko, setNaytaValikko] = useState(false);

    return (
        <CssBaseline >
            <AppBar position="static">
                <Toolbar>
                    <IconButton
                        edge="start"
                        onClick={() => { setNaytaValikko(true) }}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Drawer
                        open={naytaValikko}
                        onClose={() => { setNaytaValikko(false) }}
                    >
                        <List
                            onClick={() => { setNaytaValikko(false) }}
                        >
                            <ListItem
                                button component={Link} to="/laskentaa/"
                            >
                                <ListItemIcon>
                                    <HomeIcon />
                                </ListItemIcon>
                                <ListItemText primary="Etusivulle" />
                            </ListItem>

                            <ListItem
                                button component={Link} to="/laskuri"
                            >
                                <ListItemIcon>
                                    <InfoIcon />
                                </ListItemIcon>
                                <ListItemText primary="Laskurit" />
                            </ListItem>

                            <ListItem
                                button component={Link} to="/tehtavat"
                            >
                                <ListItemIcon>
                                    <InfoIcon />
                                </ListItemIcon>
                                <ListItemText primary="Muistilista" />
                            </ListItem>

                            <ListItem
                                button component={Link} to="/linkit"
                            >
                                <ListItemIcon>
                                    <InfoIcon />
                                </ListItemIcon>
                                <ListItemText primary="Linkkej yms" />
                            </ListItem>

                        </List>
                    </Drawer>
                    <Typography variant="h6" color="lightblue">Avaappa valikko niin lasetellaan</Typography>
                </Toolbar>
            </AppBar>
        </CssBaseline>
    )
}

export default Valikko;