import { Button, Slider, Container, FormControl, FormControlLabel, FormHelperText, FormLabel, Radio, RadioGroup, TextField, Typography } from "@mui/material";
import { useRef, useState } from "react";
import { useNavigate } from 'react-router-dom';


function Laskuri(props) {

    const lomaketiedot = useRef({});
    const [virheilmoitus, setVirheilmoitus] = useState({});

    const navigate = useNavigate();

    const lomakeKasittelija = (e) => {

        e.preventDefault();


        let virheet = {};

        if (!lomaketiedot.current.lainasumma) {
            virheet = { ...virheet, lainasumma: "lainasumma puuttuu" }
        } else {
            if (!parseFloat(lomaketiedot.current.lainasumma)>0) {
                virheet = { ...virheet, lainasumma: "Syt vain lukuja" }
            }
        }
        if (!lomaketiedot.current.aika) {
            virheet = { ...virheet, aika: "Maksuaika puuttuu" }
        }


        if (Object.entries(virheet).length > 0) {
            setVirheilmoitus({ ...virheet });
        } else {
            setVirheilmoitus({});
            props.setLasku(lomaketiedot.current)
            navigate("/tulos");
        }
    }

    const lomakeKasittelijaTuotto = (e) => {

        e.preventDefault();

        let virheet = {};

        if (!lomaketiedot.current.ssumma) {
            virheet = { ...virheet, ssumma: "Sijoitussumma puuttuu" }
        } 
        
        if (!lomaketiedot.current.saika) {
            virheet = { ...virheet, saika: "Sijoitusaika puuttuu" }
        }

        if (Object.entries(virheet).length > 0) {
            setVirheilmoitus({ ...virheet });
        } else {
            setVirheilmoitus({});
            props.setLasku(lomaketiedot.current)
            navigate("/tulostuotto");
        }
    }

    const syoteKasittelija = (e) => {
        let arvo = (e.target.type === "checkbox") ? e.target.checked : e.target.value;
        lomaketiedot.current[e.target.name] = arvo;
    }

    return (
        <Container
            style={{marginBottom:"10vh"}}
        >
            <Container>
                <Typography
                    variant="h5"
                    style={{ color: "darkred" }}
                >Lainalaskuri</Typography>
                <form onSubmit={lomakeKasittelija}>

                    <TextField
                        name="lainasumma"
                        variant="outlined"
                        label="Lainasumma"
                        fullWidth={true}
                        error={Boolean(virheilmoitus.lainasumma)}
                        helperText={virheilmoitus.lainasumma}
                        onChange={syoteKasittelija}
                        style={{ margin: 10 }}
                    />

                    <TextField
                        name="viimeera"
                        placeholder={props.lasku.viimeera}
                        variant="outlined"
                        label="Viimeinen suurempi er"
                        fullWidth={true}
                        error={Boolean(virheilmoitus.viimeera)}
                        helperText={virheilmoitus.viimeera}
                        onChange={syoteKasittelija}
                        style={{ margin: 10 }}
                    />
                    <FormControl
                        fullWidth>
                        <FormLabel>Korkotaso</FormLabel>
                        <Slider
                            onChangeCommitted={(e, newValue) => {
                                lomaketiedot.current.korko = newValue
                            }
                            }
                            aria-label="Korko"
                            //defaultValue={props.lasku.korko}
                            valueLabelDisplay="auto"
                            step={0.25}
                            marks
                            min={0.25}
                            max={7}
                        />
                    </FormControl>
                    <FormControl>
                        <FormLabel>Takaisinmaksuaika</FormLabel>
                        <RadioGroup
                            name="maksuaika"
                            row
                            onChange={syoteKasittelija}
                        >
                            <FormControlLabel labelPlacement="bottom" value="1" control={<Radio name="aika" />} label="12 kk" />
                            <FormControlLabel labelPlacement="bottom" value="2" control={<Radio name="aika" />} label="24 kk" />
                            <FormControlLabel labelPlacement="bottom" value="3" control={<Radio name="aika" />} label="36 kk" />
                            <FormControlLabel labelPlacement="bottom" value="4" control={<Radio name="aika" />} label="48 kk" />
                            <FormControlLabel labelPlacement="bottom" value="5" control={<Radio name="aika" />} label="60 kk" />
                            <FormControlLabel labelPlacement="bottom" value="6" control={<Radio name="aika" />} label="72 kk" />
                        </RadioGroup>
                        <FormHelperText
                            error={Boolean(virheilmoitus.aika)}
                        >
                            {virheilmoitus.aika}
                        </FormHelperText>
                    </FormControl>
                    <Button
                        type="submit"
                        variant="contained"
                        color="secondary"
                        size="large"
                        fullWidth="true"
                        style={{ background: "darkred" }}
                    >
                        Laske
                    </Button>

                </form>
            </Container>


            <Container>
                <Typography
                    variant="h5"
                    style={{ color: "darkgreen" }}
                >Tuottolaskuri</Typography>
                <form onSubmit={lomakeKasittelijaTuotto}>

                    <TextField
                        name="ssumma"
                        variant="outlined"
                        label="Sijoitettava summa"
                        fullWidth={true}
                        error={Boolean(virheilmoitus.ssumma)}
                        helperText={virheilmoitus.ssumma}
                        onChange={syoteKasittelija}
                        style={{ margin: 10 }}
                    />

                    <TextField
                        name="kkssumma"
                        variant="outlined"
                        label="Kuukausittain sijoitettava summa"
                        fullWidth={true}
                        error={Boolean(virheilmoitus.kkssumma)}
                        helperText={virheilmoitus.kkssumma}
                        onChange={syoteKasittelija}
                        style={{ margin: 10 }}
                    />

                    <FormControl
                        fullWidth>
                        <FormLabel>Odotettu tuottoprosentti</FormLabel>
                        <Slider
                            onChangeCommitted={(e, newValue) => {
                                lomaketiedot.current.skorko = newValue
                            }
                            }
                            aria-label="Korko"
                            valueLabelDisplay="auto"
                            step={0.25}
                            marks
                            min={0.25}
                            max={15}
                        />
                    </FormControl>
                    <FormControl>
                        <FormLabel>Sijoitettavan summan nostoon aikaa</FormLabel>
                        <RadioGroup
                            name="saika"
                            row
                            onChange={syoteKasittelija}
                        >
                            <FormControlLabel labelPlacement="bottom" value="2" control={<Radio name="saika" />} label="2 vuotta" />
                            <FormControlLabel labelPlacement="bottom" value="4" control={<Radio name="saika" />} label="4 vuotta" />
                            <FormControlLabel labelPlacement="bottom" value="6" control={<Radio name="saika" />} label="6 vuotta" />
                            <FormControlLabel labelPlacement="bottom" value="8" control={<Radio name="saika" />} label="8 vuotta" />
                            <FormControlLabel labelPlacement="bottom" value="10" control={<Radio name="saika" />} label="10 vuotta" />
                            <FormControlLabel labelPlacement="bottom" value="12" control={<Radio name="saika" />} label="12 vuotta" />
                        </RadioGroup>
                        <FormHelperText
                            error={Boolean(virheilmoitus.saika)}
                        >
                            {virheilmoitus.saika}
                        </FormHelperText>
                    </FormControl>
                    <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        size="large"
                        fullWidth="true"
                        style={{ background: "green" }}
                    >
                        Laske
                    </Button>

                </form>
            </Container>

        </Container>
    );
}

export default Laskuri;